.guide-overlayer {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99990; }

.guide-step-target {
  width: 0px;
  height: 0px;
  position: fixed;
  opacity: 1;
  cursor: pointer;
  z-index: 99991;
  top: 0;
  left: 0; }
  .guide-step-target.guide-overlayer-bgcolor {
    box-shadow: rgba(33, 33, 33, 0.8) 0px 0px 1px 2px, rgba(33, 33, 33, 0.5) 0px 0px 0px 5000px; }
  .guide-step-target .guide-tooltip {
    width: 200px;
    height: auto;
    position: absolute;
    z-index: 9993;
    cursor: pointer;
    top: 100%; }
    .guide-step-target .guide-tooltip .guide-joints {
      margin: auto; }
      .guide-step-target .guide-tooltip .guide-joints.joints-triangle {
        border-style: solid;
        border-width: 0 10px 20px 10px;
        border-color: transparent transparent #ffffff transparent;
        width: 0px;
        height: 0px; }
    .guide-step-target .guide-tooltip .guide-tooltip-main {
      width: 100%;
      background: #ffffff;
      border: 1px solid #eeeff1;
      box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.06);
      border-radius: 16px; }
      .guide-step-target .guide-tooltip .guide-tooltip-main .guide-button-box {
        overflow: hidden; }
        .guide-step-target .guide-tooltip .guide-tooltip-main .guide-button-box .guide-button {
          float: left;
          padding: 8px 10px; }
  .guide-step-target.guide-step-notarget {
    top: 50%;
    left: 50%; }
    .guide-step-target.guide-step-notarget .guide-tooltip {
      transform: translate(-50%, -50%); }
      .guide-step-target.guide-step-notarget .guide-tooltip .guide-joints {
        display: none; }
  .guide-step-target .show {
    display: block; }
  .guide-step-target .hide {
    display: none; }
